.product-grid{
   display: grid;
   grid-template-columns: repeat(auto-fit,minmax(250px,1fr));
   grid-gap:20px;
   margin-top: 70px; 
   padding: 10px;

} 
.product-card{
    display: flex;
    position: relative;
    flex-direction: column;
    align-items: center;
    padding: 10px;
    height: auto;
    border: 1px solid #ddd;
  

}
.product-card button{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 12px;
    border: none;
    background-color: #333;
    color: #fff;
    cursor: pointer;
    font-size: 16px;
    text-align: center;
    

}
.product-card button:hover{
    
    background-color: #0069d9;
    
    
}
.product-card img{
    width: 100%;
    height: 200px;
    object-fit: cover;     
}
.product-card h4{
    margin-top: 10px;
    margin-bottom: 45px;
    font-size: 18px;
    font-weight: bold;
    text-align: center;
}
.price{
    background-color: #f44336;
    color: #fff;
    font-size: 12px;
    padding: 4px;
    border-radius: 50%;
    position: absolute;
    top: 8px;
    right: 8px;
}
.product-card p{
    font-size: 16px;
    margin: 5px 0;
    font-weight: bold;
    color: #00777c;
    
}
.link{
    text-decoration: none;
    color: #fff;
}